/* ropa-sans-regular - latin */
@font-face {
  font-family: 'Ropa Sans';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/ropa-sans-v10-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('../fonts/ropa-sans-v10-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/ropa-sans-v10-latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */ url('../fonts/ropa-sans-v10-latin-regular.woff')
      format('woff'),
    /* Modern Browsers */ url('../fonts/ropa-sans-v10-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../fonts/ropa-sans-v10-latin-regular.svg#RopaSans') format('svg'); /* Legacy iOS */
}

